<template>
  <div class="px-20 py-10 text-left">
    <!-- breadcrumb -->
    <div class="py-2 text-left">
      <span class="text-cyan-600 font-bold">
        <HomeIcon class="h-4 w-4 inline" /> 首頁
      </span>
      <span class="mx-1 text-gray-600">/</span>
      <span class="text-cyan-600 font-bold">名單管理</span>
    </div>
    
    <h5 class="my-2 text-2xl font-bold title">名單管理</h5>
    
    
    <div class="accordion mt-4 border border-gray-200 bg-cyan-50 rounded-md">
      <div class="border-b border-gray-200">
        <button
          @click="toggleAccordion(1)"
          class="py-2 px-6 w-full flex items-center justify-between text-left font-bold text-cyan-600">
          篩選
          <svg
            class="w-4 h-4 transition-transform transform"
            :class="{'rotate-180': activeIndex === 1}"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 20 20"
            fill="currentColor"
            aria-hidden="true"
          >
            <path
              fill-rule="evenodd"
              d="M10 18a1 1 0 01-.707-.293l-7-7a1 1 0 111.414-1.414L10 15.586l6.293-6.293a1 1 0 111.414 1.414l-7 7A1 1 0 0110 18z"
              clip-rule="evenodd"
            />
          </svg>
        </button>
        
      </div>
      <!-- 內容 -->
      <div 
        class="px-6 py-4 bg-white rounded-b"
        v-show="activeIndex === 1"
      >
        <div>
          <div class="md:flex md:items-center mb-3">
            <div class="md:w-2/12">
              <label class="pr-4 mb-1 block text-gray-500 font-bold md:text-left md:mb-0" for="inline-account">操作時間</label>
            </div>
            <div class="md:w-6/12">
              <div class="flex items-stretch">
                <input v-model="dateStart" class="py-2 px-3 w-auto border border-gray-300 rounded-l focus:outline-none focus:border-cyan-500 z-10 flex-1"
                        type="date">
                <span class="border border-gray-300 text-gray-500 bg-gray-200 px-4 content-center">~</span>
                <input  v-model="dateEnd" class="py-2 px-3 w-auto border border-gray-300 rounded-r focus:outline-none focus:border-cyan-500 z-10 flex-1"
                        type="date">
              </div>
            </div>
          </div>
          
          <div class="mb-3 md:flex md:items-center">
            <div class="md:w-2/12">
              <label class="pr-4 mb-1 block text-gray-500 font-bold md:text-left md:mb-0" for="inline-group">操作動作</label>
            </div>
            <div class="md:w-6/12">
              <select v-model="selectedAction" class="py-2 px-4 w-full border border-gray-200 rounded text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-cyan-500 cursor-pointer">
                <option value="99">全部</option>
              </select>
            </div>
          </div>
          
          <div class="md:flex md:items-center mb-3">
            <div class="md:w-2/12">
              <label class="pr-4 mb-1 block text-gray-500 font-bold md:text-left md:mb-0">使用者名稱</label>
            </div>
            <div class="md:w-6/12">
              <input v-model="userName" class="py-2 px-4 w-full border border-gray-200 rounded text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-cyan-500" 
                      type="text" placeholder="請輸入使用者名稱">
            </div>
          </div>
          
          <div class="mb-3 md:flex md:items-center">
            <div class="md:w-2/12">
              <label class="pr-4 mb-1 block text-gray-500 font-bold md:text-left md:mb-0">備註</label>
            </div>
            <div class="md:w-6/12 flex space-x-4">
              <input v-model="note" class="py-2 px-4 w-full border border-gray-200 rounded text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-cyan-500"
                      placeholder="請輸入關鍵字">
            </div>
          </div>

          <!-- button -->
          <div class="mt-6 text-right">
            <button class="py-2 px-4 bg-cyan-600 hover:bg-cyan-700 text-white font-bold rounded-full" @click="applyFilter">
              <MagnifyingGlassIcon class="h-4 w-4 inline" /> 篩選
            </button>
            <button class="py-2 px-4 ml-3 bg-orange-400 hover:bg-orange-500 text-white font-bold rounded-full">
              <DocumentArrowDownIcon class="h-4 w-4 inline" /> 匯出
            </button>
          </div>
        
        </div>
      </div>
    </div>
    
    <!-- datatable -->
    <div class="mt-10 py-2 px-4 border rounded-md">
      <table id="listDatatable" class="display">
        <thead>
          <tr>
            <th>推薦碼</th>
            <th>會員名稱</th>
            <th>群組</th>
            <th>積點</th>
            <th>百分比</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in items" :key="item.id">
            <td>{{ item.refCode }}</td>
            <td>{{ item.username }}</td>
            <td>{{ item.group }}</td>
            <td>{{ item.point }}</td>
            <td>{{ item.percent }}</td>
            <td></td>
          </tr>
        </tbody>
      </table>
    </div>
    
  </div>
</template>

<script>
import { HomeIcon, MagnifyingGlassIcon, DocumentArrowDownIcon } from '@heroicons/vue/24/solid';
import $ from 'jquery';

export default {
  name: 'ListManage',
  components: {
    HomeIcon,
    MagnifyingGlassIcon,
    DocumentArrowDownIcon,
  },
  data() {
    return {
      activeIndex: 1, // null 表示全部收起
      dateStart: '',
      dateEnd: '',
      selectedAction: '99',
      userName: '',
      note: '',
      groupList: [
        { id: 1, text: '全部', value: '99' },
        { id: 2, text: 'Admin', value: '1' },
        { id: 3, text: 'User', value: '2' },
        { id: 4, text: 'Guest', value: '3' },
      ],
      items: [
        { id: 1, refCode: "F2407001", username: 'John Doe', group: 'Admin', point: 200, percent: "" },
        { id: 2, refCode: "F2407002", username: 'Jane Smith', group: 'User', point: 194, percent: "" },
        { id: 3, refCode: "F2407003", username: 'Paul Brown', group: 'Guest', point: 226, percent: "" },
        { id: 4, refCode: "F2407004", username: 'Mary Johnson', group: 'Admin', point: 1340, percent: "" },
        { id: 5, refCode: "F2407005", username: 'James Wilson', group: 'User', point: 850, percent: "" },
        { id: 6, refCode: "F2407006", username: 'Patricia Garcia', group: 'Guest', point: 0, percent: "" },
        { id: 7, refCode: "F2407007", username: 'Robert Martinez', group: 'Admin', point: 570, percent: "" },
        { id: 8, refCode: "F2407008", username: 'Linda Rodriguez', group: 'User', point: 880, percent: "" },
        { id: 9, refCode: "F2407009", username: 'Michael Hernandez', group: 'Guest', point: 461, percent: "" },
        { id: 10, refCode: "F2407010", username: 'Barbara Moore', group: 'Admin', point: 364, percent: "" },
        { id: 11, refCode: "F2407011", username: 'William Jackson', group: 'User', point: 778, percent: "" },
        { id: 12, refCode: "F2407012", username: 'Elizabeth Clark', group: 'Guest', point: 2240, percent: "" }
      ],
      filteredItems: [],
      table: null,
    }
  },
  methods: {
    toggleAccordion(index) {
      this.activeIndex = this.activeIndex === index ? null : index;
    },
    initializeDataTable() {
      this.table = $('#listDatatable').DataTable({
        data: this.items,
        language: {
          lengthMenu: '顯示 _MENU_ 筆',
          info: '顯示第 _START_ 到 _END_ 筆，共 _TOTAL_ 筆資料',
          infoFiltered: '(從 _MAX_ 條中過濾)',
          infoEmpty: '顯示第 0 到 0 筆，共 0 筆資料', // 可以自定義空數據時的提示
          zeroRecords: '查無資料',
          emptyTable: '查無資料',
          search: '查詢 : ',
        },
        columns: [
          { data: 'refCode', title: '推薦碼' },
          { data: 'username', title: '會員名稱' },
          { data: 'group', title: '群組' },
          { data: 'point', title: '積點' },
          { data: 'percent', title: '百分比',
            render: function (data, type, row) {
              let total = 8103;
              let num = (row.point / total) * 100;
              return num.toFixed(2) + "%";
              }
            },
            {
              title: "",
              render: function () {
                return "<div style='text-align: center;'><button class='py-1 px-2 bg-cyan-500 hover:bg-cyan-700 text-white rounded-sm'>查看更多</button></div>";
              }
            }
        ],
        order: {
          name: 'point',
          dir: 'asc'
        }
      });
    },
    applyFilter() {
      // 過濾條件
    },
  },
  mounted() {
    this.initializeDataTable();
  }
}
</script>

